import { useEffect } from "react";
import { useState } from "react";
import Header from "../components/header";
import Sidebar from "../components/sidebar";
import Footer from "../components/footer";
import { useParams } from "react-router-dom";
import Commetn from "../components/commetn"

function Rezultat() {
    const params = useParams();
    const test_id = params.id;
    const [text, setText] = useState(null);
    const points = localStorage.getItem('points');
    const TextPoints = () => {
        if (points < 40) {
            setText(" ,       .")
        }
        else if (points > 40) {
            setText("   .")
        }
        if (points < 70) {
            setText(" ,  .")
        }
        else if (points > 80) {
            setText("")
        }

    }
    const ClearPoints = () => {
        localStorage.removeItem('points');
        window.location.href = "/";
    }
    const AgainTest = () => {
        localStorage.removeItem('points')
        window.location.href = "/scrin/" + test_id + "/0"
    }
    useEffect(() => {
        TextPoints();
    }, [])
    return (
        <div>
            <Header />
            <div className="col-lg-12">

            <div className="row">
                <div className="col-lg-3">
                    <Sidebar />
                </div>

                <div className="col-lg-6 colum">
                    <h3 className="text-white"> :</h3>
                    <h5 className="text-white"> {localStorage.getItem('points')}  </h5>
                    {text}
                    <br></br>
                    <button className="buton" onClick={ClearPoints} ><b></b></button>

                    <div className="col-3">
                        <button className="buton" onClick={AgainTest} >  <br></br>  </button>
                    </div>
                </div>
                <div className="col-lg-3 colum">
                    
                </div>


                <div className="col-3"></div>
                <div className="col-lg-9 ">
                    <Commetn />
                </div>
            </div>
            <Footer />
            </div>
        </div>
    )
}
export default Rezultat;
